<template>
  <div id="myAccount" style="display:flex;flex-direction:column;align-items:center;">
      <section class="top" :style="{'background-image':`url(${headBg})`}">
        <div style="color:#fff;font-size:0.36rem">{{$t("message.MyAccount.property")}}</div>
        <div style="display:flex;align-items: center;padding-top:0.5rem; justify-content:center;">
          <span style="color:#fff;font-size:0.26rem;margin-right:0.1rem">{{this.walletAddres}}</span>
          <img :src="copyBg" style="width:0.28rem;height:0.28rem" @click="copyToBoard">
        </div>
      </section>
      <section style="background-color:#fff;width:90%;position:relative;top:-0.6rem;border-radius:0.1rem">
        <div style="font-size:0.3rem;color:#373e34;padding:0.5rem 0 ;display:flex;align-items:center;justify-content:center;"> 
          <img :src="depositBg" style="width:0.36rem;height:0.36rem;margin-right:0.2rem">
          <span style="border-right:2px solid #D8d8d8;padding-right:18%" :style="padding_right">{{$t("message.MyAccount.recharge")}}</span>
          <img :src="withdrawBg" style="width:0.36rem;height:0.36rem;padding-left:18%;margin-right:0.2rem" :style="padding_left">
          <span>{{$t("message.MyAccount.withdraw")}}</span>
        </div>
      </section>
      <section style="width:90%;">
        <div style="border-bottom:1px solid #dadfe3;display:flex;font-size:0.30rem;color:#555B61">
          <div style="width:50%;text-align:left;"><span style="margin-left:0.2rem">{{$t("message.MyAccount.currency")}}</span></div>
          <div style="width:50%;text-align:right"><span style="margin-right:0.2rem">{{$t("message.MyAccount.balance")}}</span></div>
        </div>
        <!-- list of currency balance -->
        <ul>
          <li v-show="loading" style="width:100%;height:2rem;display:flex;align-items:center;justify-content:center;">
            <van-loading type="spinner" color="white" />
          </li>
          <li v-for="(item,index) in list " :key="index" style="border-bottom:1px solid #dadfe3;display:flex;font-size:0.32rem;color:#5b636a;padding:0.2rem 0">
            <div style="width:50%;text-align:left;">
              <i :class="`currency-icon-${item.currency}`" style="position:relative;top:0.05rem;" ></i>
              <span style="margin-left:0.1rem">{{item.currency}}</span>
            </div>
            <div style="width:50%;text-align:right"><span style="margin-right:0.2rem">{{item.balance}}</span></div>
          </li>
        </ul>
      </section>
  </div>
</template>
<script>
import headBg from "@/images/myAccountBg.png";
import copyBg from "@/images/copy.png";
import depositBg from "@/images/recharge.png";
import withdrawBg from "@/images/withdraw.png";
import { BigNumber } from "bignumber.js";
import { getUserBalance } from "js/fetch";
import bus from "@/js/bus";
import { Toast } from "vant";
export default {
  name: "myAccount",
  data() {
    return {
      loading: false,
      headBg,
      copyBg,
      depositBg,
      withdrawBg,
      list: []
    };
  },
  created() {
    bus.$emit("currentRouteName", "MyAccount");
  },
  created() {
    this.getBalance();
  },
  computed: {
    getCoinList() {
      return this.$store.getters.coins;
    },
    padding_right() {
      if (this.$i18n.locale === "en") {
        return "padding-right:8%";
      }
    },
    padding_left() {
      if (this.$i18n.locale === "en") {
        return "padding-left:8%";
      }
    },
    walletAddres() {
      if (!this.$store.getters.swtAddress) {
        return "请先导入钱包";
      } else {
        return this.$store.getters.swtAddress;
      }
    }
  },
  methods: {
    async getBalance() {
      this.loading = true;
      setTimeout(async () => {
        let wallet = this.$store.getters.swtAddress;
        let balanceList = null;
        if (wallet) {
          balanceList = await getUserBalance(wallet);
          this.handleBalance(balanceList.data);
        } else {
          Toast.fail(this.$t("message.PersonalCenter.importWallet"));
        }
        this.loading = false;
      }, 300);
    },
    handleBalance(balanceList) {
      console.log(balanceList);
      let list = [];
      let coins = this.getCoinList;
      console.log(coins);
      let coinsLen = coins.length;
      let balanceLen = balanceList.length;
      let i, j;
      for (i = 0; i < coinsLen; i++) {
        for (j = 0; j < balanceLen; j++) {
          if (coins[i].name === balanceList[j].currency) {
            list.push({
              currency: coins[i].label,
              balance: new BigNumber(balanceList[j].value)
                .minus(new BigNumber(balanceList[j].freezed))
                .decimalPlaces(parseInt(coins[i].decimal))
                .toNumber()
            });
          }
        }
      }
      this.list = list;
    },
    copyToBoard() {
      this.$copyText(this.walletAddres).then(e => {
        Toast.success(this.$t("message.MyAccount.copyed"));
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.top {
  width: 100%;
  padding: 0.7rem 0 1.1rem 0;
  background-repeat: no-repeat;
  display: inline-block;
  background-size: 100% 100%;
}
.van-loading__spinner {
  color: #5b636a;
}
</style>



